:root {
  --positive-color: #22b049;
  --negative-color: #F6465D;
}
.pnl-chart-container {
  width: 95%;
  max-width: 700px;
  margin: 20px auto;
  padding: 15px 15px 5px 1px;
  background: #1d1b1c;
  border-radius: 12px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  grid-column: 1 / span 2;
  height: 300px;
}

.pnl-chart-container::before {
  display: none;
}

.pnl-chart-container:hover::before {
  display: none;
}

.pnl-chart-container h2 {
  margin-bottom: 20px;
  text-align: center;
  font-size: 1.5em;
}

.pnl-chart-container h5 {
  text-align: center;
  margin-top: -10px;
  margin-bottom: 20px;
  color: #B0B0B0;
  font-weight: normal;
}

.chart-wrapper {
  width: 90%;
  max-width: 700px;
  margin: 0 auto;
  padding: 15px 20px;
  margin-bottom: 20px;
  box-sizing: border-box;
  background: rgba(17, 18, 23, 0.7);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  height: 250px;
}

.daily-pnl-chart-container {
  width: 90%;
  max-width: 700px;
  height: 300px;
  margin: 20px auto;
  padding: 20px;
  background: #1d1b1c;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.chart-loading,
.chart-error {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 300px;
  color: #808080;
}

.chart-error {
  color: #F6465D;
}

/* Mobile styles */
@media (max-width: 768px) {
  .daily-pnl-chart-container {
    width: 95%;
    height: 300px;
    padding: 10px;
    margin: 10px auto;
  }
}

/* Desktop styles */
@media (min-width: 769px) {
  .daily-pnl-chart-container {
    width: 80%;
    max-width: 800px;
    height: 400px;
    padding: 20px;
    margin: 20px auto;
  }
}

/* If there's a title or heading above the chart, you might want to adjust its margin as well */
.daily-pnl-chart-container h2,
.daily-pnl-chart-container h3 {
  margin-bottom: 10px; /* Reduced from default value */
}

/* Adjust the chart wrapper if needed */
.daily-pnl-chart-container .chart-wrapper {
  margin-top: 5px; /* Added a small top margin */
}

.pnl-chart {
  width: 100%;
  height: 100%;
  position: relative;
  background: transparent;
  box-sizing: border-box;
}

.pnl-chart canvas {
  width: 100% !important;
  height: 100% !important;
}

.loading, .error-message {
  height: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
}

.error-message {
  color: #f6465d;
}

@media (max-width: 768px) {
  .chart-wrapper {
    width: 95%;
    padding: 15px;
    margin: 10px auto 20px;
  }
}

/* Desktop/larger screens */
@media (min-width: 1024px) {
  .chart-wrapper {
    margin-top: 20px;
  }
}

.pnl-chart-wrapper {
  width: 95%;
  max-width: 1200px;
  margin: 20px auto;
  display: flex;
  justify-content: center;
}

.full-width-chart {
  width: 100%;
  height: 300px;
  padding: 20px;
  background: #1d1b1c;
  border-radius: 12px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

/* Mobile responsiveness */
@media (max-width: 768px) {
  .pnl-chart-wrapper {
    width: 95%;
    padding: 10px;
    margin: 10px auto;
  }
  
  .full-width-chart {
    height: 250px;
    padding: 15px;
  }
}

/* Add hover effect to chart bars/points */
.recharts-layer.recharts-bar-rectangles:hover rect,
.recharts-layer.recharts-line-dots:hover circle {
  position: relative;
  transition: all 0.3s ease;
}

.recharts-layer.recharts-bar-rectangles rect:hover,
.recharts-layer.recharts-line-dots circle:hover {
  filter: drop-shadow(0 0 8px rgba(236, 72, 153, 0.3));
}

/* Add gradient effect on hover for chart elements */
.recharts-bar-rectangle::before,
.recharts-line-dot::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  background: linear-gradient(90deg, 
    rgba(236, 72, 153, 0.1), 
    rgba(234, 179, 8, 0.1), 
    transparent
  );
  opacity: 0;
  transition: opacity 0.5s ease;
  pointer-events: none;
  border-radius: inherit;
}

.recharts-bar-rectangle:hover::before,
.recharts-line-dot:hover::before {
  opacity: 1;
}

.chart-content {
  height: 100%;
  width: 100%;
  padding: 5px;
}

/* Mobile responsiveness */
@media (max-width: 768px) {
  .pnl-chart-container {
    width: 95%;
    padding: 10px 10px 5px 0;
    margin: 10px auto;
    height: 250px;
  }
  
  .chart-content {
    height: 250px;
    padding: 5px 5px 5px 0;
  }
}

