@keyframes shimmer {
  0% {
    background-position: -1000px 0;
  }
  100% {
    background-position: 1000px 0;
  }
}

.shimmer-card {
  background: #1d1b1c;
  border-radius: 12px;
  overflow: hidden;
}

.shimmer {
  animation: shimmer 2s infinite linear;
  background: linear-gradient(
    to right,
    #1d1b1c 4%,
    #2a2a2a 25%,
    #1d1b1c 36%
  );
  background-size: 1000px 100%;
}

.shimmer-text {
  height: 20px;
  width: 80%;
  margin: 4px 0;
  background: #2a2a2a;
  border-radius: 4px;
  animation: shimmer 2s infinite linear;
  background: linear-gradient(
    to right,
    #1d1b1c 4%,
    #2a2a2a 25%,
    #1d1b1c 36%
  );
  background-size: 1000px 100%;
}

.chart-shimmer {
  height: 100%;
  width: 100%;
  border-radius: 8px;
  animation: shimmer 2s infinite linear;
  background: linear-gradient(
    to right,
    #1d1b1c 4%,
    #2a2a2a 25%,
    #1d1b1c 36%
  );
  background-size: 1000px 100%;
}

.chart-shimmer-wrapper {
  width: 100%;
  height: 100%;
  padding: 20px;
  box-sizing: border-box;
}

.chart-shimmer-content {
  height: calc(100% - 40px);
  margin-top: 20px;
  width: 100%;
}
