.trade-history {
  color: #ffffff;
  font-family: Arial, sans-serif;
  padding: 15px;
  background: #1d1b1c;
  border-radius: 12px;
  width: 90%;
  max-width: 700px;
  margin: 20px auto;
  box-sizing: border-box;
  transition: all 0.3s ease;
  border: none;
}

.trade-history-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.trade-history-header h4 {
  margin: 0;
  color: #ffffff;
}

.see-all-link {
  color: #007bff;
  text-decoration: none;
}

.trade-history-table {
  width: 100%;
  border-collapse: separate;
  border-spacing: 0 8px;
}

.trade-history-table tr {
  background-color: #222021;
  border-radius: 8px;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  border: none;
}

.trade-history-table tr::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(90deg, 
    rgba(236, 72, 153, 0.1), 
    rgba(234, 179, 8, 0.1), 
    transparent
  );
  opacity: 0;
  transition: opacity 0.5s ease;
  pointer-events: none;
  border-radius: 8px;
}

.trade-history-table tr:hover::before {
  opacity: 1;
}

.trade-history-table td {
  padding: 8px 15px;
  vertical-align: middle;
  position: relative;
  z-index: 1;
}

.date-column {
  color: #8C8C8C;
  font-size: 14px;
  width: 20%;
  padding-left: 20px;
}

.name-column {
  width: 55%;
}

.currency-info {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  position: relative;
  top: 2px;
}

.currency-icon {
  font-size: 16px;
  width: 16px;
  margin-right: 8px;
}

.currency-name {
  background: linear-gradient(to right, #bf953f, #fcf6ba, #b38728, #fbf5b7, #aa771c);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  font-weight: bold;
  margin-right: 8px;
}

.type-column {
  padding-left: 0;
  width: 15%;
}

.trade-type {
  padding: 2px 6px;
  border-radius: 3px;
  font-size: 12px;
  font-weight: bold;
  text-transform: uppercase;
  display: inline-block;
}

.trade-type.buy {
  background-color: rgba(34, 176, 73, 0.15);
  color: #22b049;
}

.trade-type.sell {
  background-color: rgba(246, 70, 93, 0.15);
  color: #F6465D;
}

.pnl-column {
  text-align: right;
  font-weight: bold;
  font-size: 14px;
  width: 25%;
  padding-right: 20px;
}

.pnl-column.positive {
  color: #22b049;
}

.pnl-column.negative {
  color: #F6465D;
}

@media (max-width: 768px) {
  .trade-history {
    width: 95%;
    padding: 12px;
    margin: 10px auto;
  }

  .trade-history-table td {
    padding: 6px 10px;
  }

  .currency-icon {
    font-size: 14px;
  }

  .trade-type {
    padding: 2px 4px;
    font-size: 10px;
  }
}
