/* ----------------------------------------------------------------------------------------------------- */
/*                             Base styles (for all screen sizes)                                        */
/* ----------------------------------------------------------------------------------------------------- */

/* General app styling */
.App {
  text-align: center;
  font-family: 'DM Sans', sans-serif;
  padding-bottom: 20px;
  min-height: 100vh;
  position: relative;
  background: #0A0B0F;
}

/* Button styles */
.btn-outline {
  padding: 10px 20px;
  border: 2px solid #ffffff;
  color: #ffffff;
  text-decoration: none;
  border-radius: 5px;
  transition: all 0.3s ease;
  font-family: 'Nunito', sans-serif;
}

.btn-outline:hover {
  background-color: #ffffff;
  color: #131315;
}

/* Balance Display styles */
.balance-display {
  background-color: #1E1F25;
  color: white;
  padding: 10px; /* Reduced padding */
  margin-top: 30px; /* Adjust this value based on your header height */
  text-align: center;
  font-family: 'Roboto', sans-serif;
  height: 180px; /* Set a specific height */
}

.balance-display h3.balance-amount {
  font-size: 1.3em;
  margin: 10px 0;
  padding-top: 10px;
  background: linear-gradient(45deg, #6cdb00, #4caf50);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  -webkit-text-fill-color: transparent;
}

.balance-display p {
  margin: 10px 0;
  font-size: 0.8em;
  color: #A9A9A9;
}

.balance-display .earnings {
  font-weight: bold;
  font-size: 1em;
  margin-top: 14px;
}

.balance-display .earnings.green {
  color: #10B981;
}

.balance-display .earnings.red {
  color: #FF0000;
}

/* ----------------------------------------------------------------------------------------------------- */
/*                             Mobile styles (below 768px)                                               */
/* ----------------------------------------------------------------------------------------------------- */

@media (max-width: 767px) {
  /* Mobile-specific styles that are not header-related can go here */
}

body, #root {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  background: #0A0B0F;
  color: #ffffff;
  position: relative;
}

.gradient-background {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  z-index: -1;
  background: #0A0B0F;
}

.gradient-background::before {
  content: '';
  position: absolute;
  top: 50%;
  left: -5%;
  right: -5%;
  height: 500px;
  background: linear-gradient(90deg, 
    rgba(14, 203, 129, 0.1), 
    rgba(246, 70, 93, 0.1), 
    transparent
  );
  transform: rotate(12deg);
  filter: blur(64px);
  opacity: 0.3;
}
