.header {
  background: rgba(10, 11, 15, 0.7);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  position: relative;
  z-index: 100;
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
}

.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  max-width: 1200px;
  margin: 0 auto;
  position: relative;
}

.logo-link {
  text-decoration: none;
  margin-right: auto;
}

h4.logo {
  color: white;
  margin: 0;
  font-size: 19px;
  white-space: nowrap;
}

.wallet-button-wrapper {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1;
}

.dashboard-menu-toggle {
  background: rgba(17, 18, 23, 0.7);
  border: 1px solid rgba(255, 255, 255, 0.1);
  color: white;
  cursor: pointer;
  padding: 8px 12px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  margin-left: auto;
  z-index: 2;
}

.dashboard-menu-toggle:hover {
  background: rgba(255, 255, 255, 0.1);
  border-color: rgba(255, 255, 255, 0.2);
}

.dashboard-menu-toggle i {
  font-size: 20px;
}

.dashboard-menu-toggle.open {
  background: rgba(255, 255, 255, 0.1);
}

.navbar-toggle {
  position: absolute;
  top: calc(100% + 10px);
  right: 1rem;
  background: rgba(10, 11, 15, 0.95);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.05);
  border-radius: 12px;
  padding: 8px;
  min-width: 240px;
  z-index: 1000;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
}

.navbar-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.navbar-list li {
  margin: 4px 0;
}

.navbar-list a, .navbar-link {
  color: rgba(255, 255, 255, 0.9);
  text-decoration: none;
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 10px;
  border-radius: 8px;
  transition: all 0.2s ease;
  font-size: 0.9rem;
  font-weight: 500;
}

.menu-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 32px;
  height: 32px;
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.03);
}

.icon {
  width: 18px;
  height: 18px;
}

.menu-icon.dashboard {
  color: #60a5fa;
}

.menu-icon.trade {
  color: #a78bfa;
}

.menu-icon.transaction {
  color: #34d399;
}

.menu-icon.logout {
  color: #f87171;
}

.navbar-list a:hover, .navbar-link:hover {
  background: rgba(255, 255, 255, 0.05);
  transform: translateX(4px);
}

.navbar-list a.active {
  background: rgba(255, 255, 255, 0.07);
}

.navbar-list li:last-child {
  margin-top: 4px;
  padding-top: 4px;
  border-top: 1px solid rgba(255, 255, 255, 0.05);
}

.logout-button {
  width: 100%;
  background: none;
  border: none;
  cursor: pointer;
  color: #f87171;
}

.logout-button:hover {
  background: rgba(248, 113, 113, 0.05);
}

@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.navbar-toggle {
  animation: slideDown 0.2s ease-out;
}

@media (max-width: 768px) {
  .header-content {
    display: grid;
    grid-template-columns: auto 1fr auto;
    gap: 0.5rem;
    align-items: center;
  }

  .logo-link {
    margin-right: 0;
  }

  .wallet-button-wrapper {
    position: static;
    transform: none;
    margin-left: auto;
    margin-right: auto;
    grid-column: 2;
    justify-self: center;
  }

  .dashboard-menu-toggle {
    margin-left: 0;
    grid-column: 3;
  }

  h4.logo {
    font-size: 16px;
  }
}

/* Add this class to ensure animation works in all browsers */
.wallet-dropdown-content {
  animation: slideDown 0.2s ease-out;
  transform-origin: top;
}

.wallet-dropdown {
  animation: slideDown 0.2s ease-out;
  transform-origin: top;
}
