/* Performance Overview styles */
.performance-cards-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 15px;
    padding: 20px;
    width: 90%;
    max-width: 700px;
    margin: -10px auto 20px;
    box-sizing: border-box;
  }
  
  .performance-card {
    background: #1d1b1c;
    border-radius: 12px;
    padding: 20px;
    transition: all 0.3s ease;
    position: relative;
    overflow: hidden;
    border: none;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  .performance-card::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(90deg, 
      rgba(236, 72, 153, 0.1), 
      rgba(234, 179, 8, 0.1), 
      transparent
    );
    opacity: 0;
    transition: opacity 0.5s ease;
    pointer-events: none;
  }
  
  .performance-card:hover::before {
    opacity: 1;
  }
  
  .card-header {
    display: flex;
    align-items: center;
    gap: 8px;
    position: relative;
    z-index: 1;
  }
  
  .card-icon {
    width: 24px;
    height: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(14, 203, 129, 0.1);
    border-radius: 6px;
  }
  
  .card-icon.negative {
    background: rgba(246, 70, 93, 0.1);
  }
  
  .card-icon i {
    font-size: 14px;
    color: #0ECB81;
  }
  
  .card-icon.negative i {
    color: #F6465D;
  }
  
  .card-title h3 {
    color: #ffffff;
    font-size: 14px;
    font-weight: 400;
    margin: 0;
    opacity: 0.7;
  }
  
  .card-stats {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    flex-grow: 1;
    margin-top: 20px;
    position: relative;
    z-index: 1;
  }
  
  .stat-value {
    font-size: 32px;
    font-weight: 500;
    margin-bottom: 8px;
    letter-spacing: -0.5px;
    position: relative;
    z-index: 1;
    text-align: center;
    width: 100%;
  }
  
  .stat-value.positive {
    color: #22b049;
  }
  
  .stat-value.negative {
    color: #F6465D;
  }
  
  .stat-label {
    color: #808080;
    font-size: 12px;
    font-weight: 400;
    text-transform: uppercase;
    letter-spacing: 0.5px;
    opacity: 0.7;
    position: relative;
    z-index: 1;
    text-align: center;
    width: 100%;
  }
  
  @media (max-width: 768px) {
    .performance-cards-container {
      width: 95%;
      padding: 10px;
      gap: 10px;
      margin: 0 auto;
    }
  
    .performance-card {
      padding: 12px;
    }
  
    .card-header {
      display: flex;
      align-items: center;
      gap: 6px;
    }
  
    .card-icon {
      width: 20px;
      height: 20px;
      min-width: 20px;
    }
  
    .card-title {
      margin-left: -2px;
    }
  
    .card-title h3 {
      font-size: 13px;
      margin-bottom: 0;
    }
  
    .card-stats {
      margin-top: 15px;
      text-align: center;
    }
  
    .stat-value {
      font-size: 24px;
      margin-bottom: 4px;
      text-align: center;
    }
  
    .stat-label {
      font-size: 11px;
      text-align: center;
    }
  }
  
  .performance-overview {
    padding: 20px;
    margin-top: 20px;
  }
  
  .trading-cards {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    max-width: 800px;
    margin: 0 auto;
  }
  
  .trading-card {
    background: #242830;
    border-radius: 12px;
    padding: 20px;
    transition: transform 0.3s ease;
  }
  
  .trading-card:hover {
    transform: translateY(-5px);
  }
  
  .card-header {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
  }
  
  .card-icon {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: rgba(76, 175, 80, 0.1);
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 15px;
  }
  
  .card-icon i {
    color: #22b049;
    font-size: 18px;
  }
  
  .card-stats {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  
  .stat-value {
    font-size: 24px;
    font-weight: bold;
    color: #4CAF50;
  }
  
  .stat-label {
    font-size: 14px;
    color: #808080;
    margin-top: 5px;
  }
  
  @media (max-width: 768px) {
    .trading-cards {
      grid-template-columns: 1fr;
      gap: 15px;
    }
    
    .trading-card {
      padding: 15px;
    }
  }
  
  
  
  