.dashboard-container {
  min-height: 100vh;
  position: relative;
  padding: 20px;
  box-sizing: border-box;
  z-index: 0;
}

.gradient-background {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  z-index: -1;
  background: #0A0B0F;
}

.gradient-background::before {
  content: '';
  position: absolute;
  top: 50%;
  left: -5%;
  right: -5%;
  height: 500px;
  background: linear-gradient(90deg, 
    rgba(236, 72, 153, 0.2), 
    rgba(234, 179, 8, 0.2), 
    transparent
  );
  transform: rotate(12deg);
  filter: blur(64px);
  opacity: 0.5;
}

.dashboard-content {
  position: relative;
  z-index: 1;
}

.dashboard-section {
  position: relative;
  z-index: 1;
}

.dashboard-section:first-child {
  margin-top: 20px;
}

.loading, .error {
  text-align: center;
  padding: 20px;
  color: #ffffff;
  font-size: 16px;
}

.error {
  color: #F6465D;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .dashboard-container {
    padding: 10px;
  }
  
  .dashboard-section {
    margin-bottom: 20px;
  }
}

/* Update header background */
.header {
  background: #0A0B0F;
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
}
