.login-wrapper {
  min-height: 100vh;
  width: 100%;
  background-color: #111111;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  background-image: 
    linear-gradient(45deg, #1a1a1a 25%, transparent 25%),
    linear-gradient(-45deg, #1a1a1a 25%, transparent 25%),
    linear-gradient(45deg, transparent 75%, #1a1a1a 75%),
    linear-gradient(-45deg, transparent 75%, #1a1a1a 75%);
  background-size: 20px 20px;
  background-position: 0 0, 0 10px, 10px -10px, -10px 0px;
}

.login-box {
  width: 100%;
  max-width: 320px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  margin: 0;
  padding: 0;
}

.login-content {
  background-color: #1E1E1E;
  border-radius: 12px;
  padding: 1.5rem;
  width: 100%;
  box-sizing: border-box;
}

.login-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
}

.login-title {
  font-size: 1.25rem;
  font-weight: 600;
  color: white;
  margin: 0;
}

.login-close-button {
  color: #71717a;
  background: none;
  border: none;
  padding: 0.25rem;
  border-radius: 9999px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s;
}

.login-close-button:hover {
  color: white;
  background-color: #27272a;
}

.login-form {
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
}

.login-form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.login-label {
  display: block;
  font-size: 0.875rem;
  color: #666;
  margin-bottom: 0.5rem;
}

.login-input {
  width: 100%;
  padding: 0.5rem 0.75rem;
  background-color: #2A2A2A;
  border-radius: 0.375rem;
  color: white;
  border: 1px solid transparent;
  transition: all 0.2s;
  box-sizing: border-box;
}

.login-input:-webkit-autofill,
.login-input:-webkit-autofill:hover,
.login-input:-webkit-autofill:focus {
  -webkit-text-fill-color: white;
  -webkit-box-shadow: 0 0 0px 1000px #2A2A2A inset;
  transition: background-color 5000s ease-in-out 0s;
}

.login-input::placeholder {
  color: #71717a;
}

.login-input:hover {
  border-color: #3f3f46;
}

.login-input:focus {
  border-color: #22c55e;
  outline: none;
}

.login-submit-button {
  width: 100%;
  background-color: #22c55e;
  color: white;
  font-weight: 500;
  padding: 0.75rem 1rem;
  border: none;
  border-radius: 0.375rem;
  cursor: pointer;
  transition: all 0.2s;
  margin-top: 0.5rem;
}

.login-submit-button:hover {
  background-color: #16a34a;
}

.login-submit-button:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

.login-divider {
  margin: 1.5rem 0;
  display: flex;
  align-items: center;
  text-align: center;
  color: #71717a;
}

.login-divider::before,
.login-divider::after {
  content: '';
  flex: 1;
  border-top: 1px solid #3f3f46;
}

.login-divider span {
  padding: 0 0.75rem;
  font-size: 0.875rem;
}

.login-google-button {
  width: 100%;
  background-color: #2A2A2A;
  color: white;
  border: none;
  border-radius: 0.375rem;
  padding: 0.75rem 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  cursor: pointer;
  transition: all 0.2s;
  margin-top: 0.5rem;
}

.login-google-button:hover {
  background-color: #3f3f46;
}

.login-google-icon {
  width: 1.25rem;
  height: 1.25rem;
}

.login-links {
  margin-top: 1rem;
  text-align: center;
  font-size: 0.875rem;
}

.login-signup-text {
  color: #666;
  margin-bottom: 0.5rem;
}

.login-link {
  color: #22c55e;
  text-decoration: none;
  transition: all 0.2s ease;
}

.login-link:hover {
  color: #16a34a;
}

.login-forgot-link {
  color: #666;
  text-decoration: none;
  display: block;
  margin-top: 0.5rem;
  transition: all 0.2s ease;
}

.login-forgot-link:hover {
  color: #999;
}

.login-error {
  background-color: rgba(239, 68, 68, 0.1);
  color: #ef4444;
  text-align: center;
  padding: 0.75rem;
  border-radius: 0.375rem;
  margin-bottom: 1rem;
  font-size: 0.875rem;
}

@media (max-width: 480px) {
  .login-box {
    width: calc(100% - 40px);
  }
  
  .login-content {
    padding: 1.25rem;
  }
}
